import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { regUser } from "../login/Login";

function validate (values,operation){
    const errors={};
    if (!values.email) {
     errors.email = 'Required';
   } else if (
     !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
   ) {
     errors.email = 'Invalid email address';
   }
   if (!values.password){
    errors.password = 'Required';
   } else if (
    !/^[A-Z0-9._%+-]{2,}$/i.test(values.password)
  ) {
    errors.password = 'Invalid password';
  }
  if (!values.name && operation==="Register"){
    errors.name = 'Required';
   } else if (
    !/^[A-Z0-9._%+-]{2,}$/i.test(values.name) && operation==="Register"
  ) {
    errors.name = 'Invalid name';
  }
   return errors;
}


const AuthForm = () => {
  const [operation, setOperation] = useState("Login");
  return (
    <Formik
      initialValues={{ email: "", password: "", name: "" }}
      validate={(values)=>validate(values,operation)}
      onSubmit={ async (user) => {
          console.log(user)
        if (operation === "Register") {
            const regResponse = await regUser(//не доделанно только регистрация 
        {
          username: user.email,
          password: user.password,
          role:"USER",
          name: user.name
        },
        "POST"
      );
        console.log(regResponse)
      if (regResponse === false) {
        alert("Email already registered");
      }
    }
      }}
    >
      {({errors}) => {
          console.log(errors) 
          return( 
        
          <Form>
            <Field name="email" />
            {errors ? errors.email:null}
            <Field name="password" />
            {errors ? errors.password:null}
            <label>
              <input
                type="radio"
                onChange={(e) => setOperation("Register")}
                checked={operation === "Register"}
                name="radio"
                value="sign up"
              />
              Sign up
              
            </label>

            <label>
              <input
                type="radio"
                onChange={(e) => setOperation("Login")}
                checked={operation === "Login"}
                name="radio"
                value="sign in"
              />
              Sign in
            </label>
            <Field name="name" />
            {errors ? errors.name:null}
            <button type="submit">Submit</button>
          </Form>
        
      )}}
    </Formik>
  );
};

export default AuthForm;
